header {
  padding: 30px 108px;
  background-color: white !important;
}

header .logo {
  object-fit: contain;
}

header ul {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

header ul li {
  padding-left: 27px;
}

header ul img {
  height: 34x;
  width: 34px;
  padding-right: 8px;
}

footer {
  background-color: white !important;
}

footer h5, footer .h5 {
  padding: 25px 0;
  text-align: center;
  vertical-align: center;
  color: #ACACAC;
  font-size: 12px !important;
}

.error {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  height: 20vh;
}

.error .error-box {
  margin-top: 70px;
  display: flex;
}

.error .error-box img {
  width: 55px;
  height: 55px;
  margin-right: 13px;
}

.error .error-box .words h2, .error .error-box .words .h2 {
  font-size: 20px;
}

.error .error-box .words h5, .error .error-box .words .h5 {
  font-size: 18px;
}

.area-title {
  display: flex;
  margin-bottom: 12px !important;
}

.area-title img {
  padding-right: 14px;
}

.area-title h2, .area-title .h2 {
  font-size: 20px;
}

.city-pagination {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.city-pagination .spot-single {
  width: 19%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 4px 3px rgba(13, 11, 12, 0.2);
}

.city-pagination .spot-single .mask {
  width: 100%;
  position: absolute;
  top: 1%;
  bottom: 0.5%;
  background: #0D0B0C;
  opacity: 0.3;
  border: 10px solid white;
}

.city-pagination .spot-single .pic {
  width: 100%;
  max-height: 245px;
  object-fit: cover;
  border: 10px solid white;
}

.city-pagination .spot-single .city-name {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.city-pagination .spot-single .city-name img {
  width: 30px;
}

.city-pagination .spot-muti {
  width: 19%;
}

.city-pagination .spot-muti .spot {
  width: 100%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 4px 3px rgba(13, 11, 12, 0.2);
}

.city-pagination .spot-muti .spot .mask {
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  background: #0D0B0C;
  opacity: 0.3;
  border: 10px solid white;
}

.city-pagination .spot-muti .spot .pic {
  width: 100%;
  max-height: 122px;
  background: url("./../images/city_5.png");
  background-size: cover !important;
  border: 10px solid white;
}

.city-pagination .spot-muti .spot .city-name {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.city-pagination .spot-muti .spot .city-name img {
  width: 30px;
}

.activity-card-group {
  display: flex;
  justify-content: space-between;
  margin-bottom: 48px !important;
}

.activity-card-group .activity-card {
  display: flex;
  justify-content: space-between;
  padding: 16px;
  box-shadow: 1px 2px 6px 2px rgba(13, 11, 12, 0.2);
}

.activity-card-group .activity-card > img {
  width: 35%;
  object-fit: cover;
}

.activity-card-group .activity-card .card-info {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.activity-card-group .activity-card .card-info p {
  flex-grow: 1;
  color: #ACACAC;
}

.activity-card-group .activity-card .card-info .more {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.activity-card-group .activity-card .card-info .more .place {
  display: flex;
  align-items: center;
}

.food-card-group {
  display: flex;
  justify-content: space-between;
  margin-bottom: 35px !important;
}

.food-card-group .food-card {
  padding: 12px 12px 16px;
  box-shadow: 1px 2px 6px 2px rgba(13, 11, 12, 0.2);
}

.food-card-group .food-card > img {
  min-width: 100%;
  height: 130px;
  object-fit: cover;
}

.food-card-group .food-card .place {
  display: flex;
  align-items: center;
  color: #007350;
  font-weight: 300;
}

.search {
  height: 490px;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 10px solid white;
  box-shadow: 1px 2px 6px 2px rgba(13, 11, 12, 0.2);
}

.search.bg-index {
  background-image: url("./../images/search_sv1.png");
}

.search.bg-food-hotel {
  background-image: url("./../images/search_sv3.png");
}

.search.bg-taiwan-spot {
  background-image: url("./../images/search_sv2.png");
}

.search.bg-transport {
  height: 170px;
  background-color: white;
}

.search .search-box .search-title {
  object-fit: cover;
}

.search .search-box .search-subtitle {
  color: white;
  font-size: 14px;
  margin-bottom: 9px;
}

.search .search-box .search-keywords {
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
}

.search .search-box .search-keywords input {
  flex-grow: 1;
  border: none;
  box-shadow: 0px 4px 3px rgba(13, 11, 12, 0.2);
  border-radius: 6px;
  margin-right: 6px;
}

.search .search-box .search-keywords img {
  object-fit: cover;
}

.search .search-box .search-select {
  display: flex;
  justify-content: space-between;
}

.search .search-box .search-select select {
  height: 40px;
  padding: 8px 16px !important;
  flex-grow: 1;
  border: none;
  border-radius: 6px;
  letter-spacing: 2px;
  margin-right: 6px;
}

.search .search-box .search-select select.shadow {
  box-shadow: 1px 2px 6px 2px rgba(13, 11, 12, 0.2);
}

.search .search-box .search-select img {
  object-fit: cover;
}
